import PropTypes from 'prop-types'
import Button from './Button'

const Header = ({ title, onAdd, showAdd }) => {
    return (
        <header className='header'> 
            <h1>{title}</h1>
            <Button 
                color={showAdd ? 'red' : 'green'} 
                text={showAdd ? 'Close' : 'Add'} 
                onClick={onAdd}/>
        </header>
    )
}

Header.defaultProps = {
    title: 'Task Tracker',
}

// Make your react component more robust to specify the proptypes
// will still render but errors in console + isRequired
Header.propTypes = {
    title: PropTypes.string.isRequired,
}

// CSS in JS 1) Can create different variable: style = {} 2) Can use inline: style = {{}}
// const headingStyle = {
//     color: 'red',
//     backgroundColor: 'black'
// }

export default Header
